.Header {
	margin: 0;
	/*padding: 1% 0;*/
	width: 100%;
	text-align: center;
	/*background-color: #febf5e;*/
	background-image: -webkit-gradient(linear, left top, right bottom, from(#f9ba32), to(orange));
	background-image: -webkit-linear-gradient(top left, #f9ba32, orange);
	background-image: -o-linear-gradient(top left, #f9ba32, orange);
	background-image: linear-gradient(to bottom right, #f9ba32, orange);
	color: white;
}



